import Vue from "vue";
import Vuex from "vuex";
import { fr } from "@/lang";
import { en } from "@/lang";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    status: "",
    cartItems: localStorage.getItem("cartItems") || [],
    token: localStorage.getItem("token") || "",
    user: {},
    currentProfile: {},
    isLoading: false,
    requestFlag: null,
    drawerOpen: true,
    paginationRecords: [],
    projectMediaPath: "uploads/products",
    entityImagePost: {
      record_id: null,
      url: null,
    },
    deleteAlert: {
      title: null,
      content: null,
      actionType: null,
      actionButton: null,
      classes: null,
      modalOpen: false,
    },
    currentDeletePopOver: null,
    recordDeleter: {
      entityName: null,
      url: null,
      media: {
        recordImage: null,
        galleryTable: null,
        mediaPath: null,
        galleryParentRef: null,
      },
    },
    currentContent: {},
    isEditingMode: false,
    hasActiveModal: false,
    flashMessage: {},
    settings: {
      base_color: null,
      secondary_color: null,
      club_logo: null,
      favicon: null,
      site_address: null,
      site_name: null,
      currency: null,
      contact_one: null,
      app_phone: null,
      app_email: null,
      home_stadium: null,
      club_name: null
    },
    authSession: {
      action: null,
      redirect_url: null,
    },
    ads: [],
  },

  mutations: {
    CREATE_AUTH_SESSION(state, data) {
      state.authSession = data;
    },
    SET_APP_SETTINGS(state, settings) {
      state.settings = settings;
    },
    SET_REQUEST_FLAG(state, flag) {
      state.requestFlag = flag;
    },
    SET_FLASH_MESSAGE(state, message) {
      state.flashMessage = message;
    },
    logout(state) {
      state.status = "";
      state.token = "";
    },
    SET_CURRENT_USER(state, user) {
      state.user = user;
    },
    CART_REQUEST(state) {
      state.status = "cartloading";
    },
    CART_SUCCESS(state, cartItems) {
      state.status = "cartsuccess";
      state.cartItems = cartItems;
    },
    CART_CLEAR(state) {
      state.status = "";
      state.cartItems = [];
    },
    SET_ADS(state, ads) {
      state.ads = ads;
    },
    REQUEST_RESOLVED(state) {
      state.isLoading = false;
      state.requestFlag = null;
      if (state.hasActiveModal) {
        state.hasActiveModal = false;
        document.querySelector("#alert-container").remove();
      }
    },
    REGISTER_CONFIRM_MODAL(state) {
      state.requestFlag = "HANDLING_ALERT_ACTION";
      state.hasActiveModal = true;
    }
  },
  actions: {
    setCurrentUser({ commit }, user) {
      return new Promise((resolve) => {
        commit("SET_CURRENT_USER", user);
        resolve();
      });
    },
    logout({ commit }) {
      return new Promise((resolve) => {
        commit("logout");
        localStorage.clear();
        resolve();
      });
    },
    cartRequest({ commit }, dataParams) {
      return new Promise((resolve) => {
        commit("CART_REQUEST");
        let available = 0;
        const itemsName =
          dataParams.isLoan === true ? "loanCartItems" : "cartItems";
        let cartItems = localStorage.getItem(itemsName);
        if (cartItems) {
          let temp = JSON.parse(cartItems);
          temp.forEach((arrItem) => {
            if (arrItem.product_id === dataParams.product_id) {
              var objectsAreSame = true;
              for (var elt in arrItem.options) {
                if (arrItem.options[elt] !== dataParams.options[elt]) {
                  objectsAreSame = false;
                  break;
                }
              }
              if (objectsAreSame) {
                arrItem.qty = Number(arrItem.qty) + Number(dataParams.qty);
                available++;
              }
            }
          });
          if (available === 0) {
            temp.push(dataParams);
          }
          localStorage.setItem(itemsName, JSON.stringify(temp));
          commit("CART_SUCCESS", temp);
        } else {
          let temp = [];
          temp.push(dataParams);
          localStorage.setItem(itemsName, JSON.stringify(temp));
          commit("CART_SUCCESS", temp);
        }
        resolve();
      });
    },

    cartRemove({ commit }, dataParams) {
      return new Promise((resolve) => {
        commit("CART_REQUEST");
        const itemsName =
          dataParams.isLoan === true ? "loanCartItems" : "cartItems";
        let cartItems = localStorage.getItem(itemsName);

        if (cartItems) {
          let temp = JSON.parse(cartItems);
          temp.forEach((arrItem) => {
            if (arrItem.product_id === dataParams.product_id) {
              let idx = temp.indexOf(arrItem);
              temp.splice(idx, 1);
            }
          });
          localStorage.setItem(itemsName, JSON.stringify(temp));
          commit("CART_SUCCESS", temp);
        }
        resolve();
      });
    },

    clearCart({ commit }) {
      return new Promise((resolve) => {
        commit("CART_CLEAR");
        localStorage.removeItem("cartItems");
        resolve();
      });
    },
    cartQtyRequest({ commit }, dataParams) {
      return new Promise((resolve) => {
        commit("CART_REQUEST");
        const itemsName =
          dataParams.isLoan === true ? "loanCartItems" : "cartItems";
        let cartItems = localStorage.getItem(itemsName);

        let temp = JSON.parse(cartItems);
        temp.forEach((arrItem) => {
          if (arrItem.product_id === dataParams.product_id) {
            arrItem.qty = Number(dataParams.qty);
          }
        });
        localStorage.setItem(itemsName, JSON.stringify(temp));
        commit("CART_SUCCESS", temp);
        resolve();
      });
    },
  },
  getters: {
    isLoggedIn: (state) => !!state.token,
    authStatus: (state) => state.status,
    isEditingMode: (state) => !!state.isEditingMode,
    user: (state) => state.user,
    paginationRecords: (state) => state.paginationRecords,
    currentProfile: (state) => state.currentProfile,
    cartItems: (state) => state.cartItems,
    lang: (state) => {
      return state.currentLanguage == "en" ? en : fr;
    },
  },
});
