import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import "nprogress/nprogress.css";
import store from "../store";
import { adminRoutes } from "./admin";
import { frontRoutes } from "./front";
import { tvplusRoutes } from "./tvplus";
import { apxRoutes } from "./apx";
const options = {
  latencyThreshold: 200,
  router: true,
  http: false,
};
Vue.use(NProgress, options);
Vue.use(Router);

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [...frontRoutes, ...adminRoutes, ...tvplusRoutes, ...apxRoutes],
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});

router.beforeEach((to, from, next) => {
  NProgress.start();
  //const token = localSto
  if (to.matched.some((record) => record.meta.requiresUserAuth)) {
    if (store.getters.isLoggedIn) {
      next();
      return;
    }
    window.location.replace("/admin/login");
  } else if (to.matched.some((record) => record.meta.requiresAdminAuth)) {
    if (store.getters.isLoggedIn) {
      next();
      return;
    }
    window.location.replace("/admin/login");
  } else {
    next();
  }
});

const DEFAULT_TITLE = "Hyper Sports";
router.afterEach((to) => {
  NProgress.done();
  Vue.nextTick(() => {
    document.title = to.meta.title + " | " + DEFAULT_TITLE || DEFAULT_TITLE;
    store.state.drawerOpen = false;
    [...document.querySelectorAll("div.dropdown-menu")].forEach((elt) => {
      elt.classList.remove("show");
    });
  });
});

export default router;
