<template>
  <button
    class="zoho-btn blue"
    id="nextbtn"
    type="button"
    tabindex="2"
    @click="
      $store.state.requestFlag = activator;
      $emit('btn-role');
    "
    :class="{
      changeloadbtn:
        $store.state.isLoading && $store.state.requestFlag === activator,
    }"
    :disabled="$store.state.isLoading || disabler"
  >
    <span class="" v-if="$store.state.requestFlag !== activator">{{
      caption
    }}</span>
  </button>
</template>
<script>
export default {
  name: "ZohoButton",
  props: {
    caption: {
      type: String,
      required: true,
    },
    activator: {
      type: String,
      required: true,
    },
    disabler: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
<style lang="css" scoped>
.zoho-btn {
  cursor: pointer;
  display: block;
  width: 100%;
  height: 44px;
  border-radius: 4px;
  letter-spacing: 0.5;
  font-size: 14px;
