export const globalMethods = {
  data() {
    return {
      apiURL:
        process.env.NODE_ENV === "production"
          ? "https://digital.tsindamag.com/api/uploads"
          : "http://127.0.0.1:8000/uploads",
      redirectURL:
        process.env.NODE_ENV === "production"
          ? "https://digital.tsindamag.com/thank-you/"
          : "http://localhost:8080/thank-you/",
      formTitle: null,
      formSubtitle: null,
      alertDefaults: {
        title: null,
        content: null,
        actionType: null,
        actionButton: null,
        classes: null,
        modalOpen: false,
      },
      choosenRow: {},
      modalOpen: false,
      filterKey: null,
      publicPath: process.env.BASE_URL,
    };
  },
  computed: {
    siteSettings() {
      return this.$store.state.settings;
    },
    appSettings() {
      let settings = this.$store.state.settings || {};
      return settings.result || {};
    },
    appLogo() {
      return this.appSettings.club_logo
        ? `${this.apiURL}/${this.appSettings.club_logo}`
        : "/img/default-logo.png";
    },
    isUserLoggedIn: function () {
      return this.$store.getters.isLoggedIn;
    },
    authSession() {
      return this.$store.state.authSession || {};
    },
    filteredRecords: function () {
      let filterKey = this.filterKey && this.filterKey.toLowerCase();
      let records = this.records;
      if (filterKey) {
        records = records.filter((row) => {
          return Object.keys(row).some((key) => {
            return String(row[key]).toLowerCase().indexOf(filterKey) > -1;
          });
        });
      }
      return records;
    },
  },
  methods: {
    getSettings() {
      this.$http
        .post(
          "ghost/preloaders",
          this.formData({ keys: Object.keys(this.siteSettings) })
        )
        .then((response) => {
          this.$store.commit("SET_APP_SETTINGS", response.data);
        });
    },
    formData(obj) {
      let formData = new FormData();
      for (let key in obj) {
        if (obj[key] !== null) {
          if (typeof obj[key] === "object")
            formData.append(key, JSON.stringify(obj[key]));
          else formData.append(key, obj[key]);
        }
      }
      return formData;
    },
    signout() {
      this.$store.dispatch("logout").then(() => {
        window.location.replace("/admin/login");
      });
    },
    closeAlert() {
      this.alertDefaults.modalOpen = false;
    },
    toggleModalOpen() {
      const elt = document.documentElement;
      if (elt.classList.contains("modal-open"))
        elt.classList.remove("modal-open");
      else elt.classList.add("modal-open");

      const el = document.body;
      if (el.classList.contains("modal-open"))
        el.classList.remove("modal-open");
      else el.classList.add("modal-open");
    },
    toggleModal() {
      this.toggleModalOpen();
      this.modalOpen = !this.modalOpen;
    },
    clearObject(obj) {
      for (let key in obj) {
        if (obj[key] === true) obj[key] = false;
        else if (obj[key] === false) continue;
        else obj[key] = null;
      }
      return obj;
    },
    selectRecord(record) {
      this.choosenRow = record;
    },
    isEqual(arrLen, key) {
      return key === arrLen - 1;
    },
    isEmpty(obj) {
      const isNULL = Object.values(obj).every((x) => x === null);
      return Object.keys(obj).length === 0 || isNULL === true;
    },
    addComma(num) {
      if (num) {
        if (num.toString().indexOf(".") > -1) num = Number(num).toFixed(2);
      }
      let regexp = /\B(?=(\d{3})+(?!\d))/g;
      return Number(num) ? num.toString().replace(regexp, ",") : 0;
    },
    salePrice(orginal_price, sale_percentage) {
      return this.floatFormatter(
        orginal_price - (sale_percentage * orginal_price) / 100
      );
    },
    floatFormatter(number) {
      let num = number.toString();
      let numParts = num.split(".");
      let lastPart = numParts[1] ? "." + numParts[1].slice(0, 2) : "";
      return parseInt(Number(numParts[0])) + lastPart;
    },
    computePercentage(regular_price, price) {
      return (100 * (regular_price - price)) / regular_price;
    },
    salePriceT(orginal_price, sale_percentage) {
      return this.floatFormatter(
        orginal_price + (sale_percentage * orginal_price) / 100
      );
    },
    incrementObjectKey(obj, key, max = null) {
      if (max) {
        if (Number(obj[key]) <= max) {
          obj[key] = Number(obj[key]) + 1;
        }
      } else {
        obj[key] = Number(obj[key]) + 1;
      }
    },
    decrementObjectKey(obj, key, min = 1) {
      if (Number(obj[key]) > min) {
        obj[key] = Number(obj[key]) - 1;
      }
    },
    removeComma(str) {
      return Number(str.replace(/,/g, ""));
    },
    uuidv4() {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
        (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16)
      );
    },
    cartesianProduct(arr) {
      return arr.reduce(
        (a, b) => {
          return a
            .map((x) => {
              return b.map((y) => {
                return x.concat(y);
              });
            })
            .reduce((a, b) => {
              return a.concat(b);
            }, []);
        },
        [[]]
      );
    },
    chatDate(str) {
      let options = {
        month: "short",
        day: "numeric",
        year: "numeric",
      };
      let today = new Date(str);
      return today.toLocaleDateString("en-US", options);
    },
    chatMiniDate(str) {
      let options = {
        month: "short",
        day: "numeric",
        year: "numeric",
        hour: "numeric",
        minute: "numeric",
        hour12: true,
      };
      let today = new Date(str);
      return today.toLocaleDateString("en-US", options);
    },
    FormatDate(givenDate, givenTime = null) {
      let dateString =
        givenTime !== null ? `${givenDate} ${givenTime}` : givenDate;
      let date = new Date(dateString);
      let options = {
        weekday: "short",
        year: "numeric",
        month: "short",
      };
      if (givenTime) {
        return date.toLocaleTimeString("en-US", {
          ...options,
          ...{ day: "numeric", hour: "2-digit", minute: "2-digit" },
        });
      }
      return date.toLocaleDateString("en-US", options);
    },
    reloadPage() {
      window.location.reload();
    },
    togglePasswordVisibility(inputId) {
      let elt = document.getElementById(inputId);
      if (elt.type === "password") elt.type = "text";
      else elt.type = "password";
      if (document.getElementById("togglePassword")) {
        document
          .getElementById("togglePassword")
          .classList.toggle("os-icon-eye-off");
      }
    },
    showFilePicker(inputFileId) {
      document.getElementById(inputFileId).click();
    },
    handleEntityImageModify(args = {}) {
      this.$store.dispatch("SetEntityPostData", args).then(() => {
        document.getElementById("global-upload").click();
      });
    },
    isAuthenticate(userRole) {
      return this.$store.state.user.role_id == userRole;
    },
    empty(mixedVar) {
      let undef, key, i, len;
      const emptyValues = [undef, null, false, 0, "", "0"];
      for (i = 0, len = emptyValues.length; i < len; i++) {
        if (mixedVar === emptyValues[i]) {
          return true;
        }
      }
      if (typeof mixedVar === "object") {
        for (key in mixedVar) {
          if (Object.prototype.hasOwnProperty.call(mixedVar, key)) {
            return false;
          }
        }
        return true;
      }
      return false;
    },
    handleDeleteAlertModal(args) {
      this.$store.state.recordDeleter.entityName = args.tableName;
      this.$store.state.recordDeleter.url = args.url;
      if (!this.empty(args.media)) {
        this.$store.state.recordDeleter.media = { ...args.media };
      }
      if (!this.empty(args.callback)) {
        this.$store.state.recordDeleter.callback = args.callback;
      }
      this.$store.state.deleteAlert = {
        title: "Deleting a record",
        content:
          "Are you sure you want to delete this " +
          args.entityIdentifer +
          ": <b>" +
          args.entity +
          "?</b> <br/> If you continue, there is no way to undo change you have made.",
        actionButton: "Yes, delete",
        modalOpen: true,
      };
    },
    deletePaginationRecord() {
      let recordDeleter = this.$store.state.recordDeleter;
      this.$store.state.requestFlag = "deletingRecord";
      this.$http.get(recordDeleter.url).then((response) => {
        if (!response.data.error) {
          this.$store.state.deleteAlert.modalOpen = false;
          let callback = this.$store.state.recordDeleter.callback;
          if (!this.empty(callback)) {
            callback();
            delete this.$store.state.recordDeleter.callback;
          } else window.location.reload();
        }
      });
    },
    cc_format(value) {
      var v = value.replace(/\s+/g, "").replace(/[^0-9]/gi, "");
      var matches = v.match(/\d{4,16}/g);
      var match = (matches && matches[0]) || "";
      var parts = [];

      for (let i = 0, len = match.length; i < len; i += 4) {
        parts.push(match.substring(i, i + 4));
      }

      if (parts.length) {
        return parts.join(" ");
      } else {
        return value;
      }
    },
  },
};
